import { API_ORIGIN, API_URL } from '../constants';
import { XRAnalytics, DataStreamCredentials, MetricSurveysData } from '../types';

export async function getXRAnalyticsDataStream(data: {
  appkey: string;
  sessionId?: string;
  clientIPAddress?: string;
}): Promise<DataStreamCredentials | undefined> {
  let headers: {
    [keys: string]: string;
  } = {};
  headers['Content-Type'] = 'application/json';
  if (data.clientIPAddress) {
    headers['X-Forwarded-For'] = data.clientIPAddress;
  }

  const response = await fetch(`${API_URL}/data-stream/xr-analytics`, {
    method: 'POST',
    keepalive: true,
    headers,
    body: JSON.stringify({ appkey: data.appkey, sessionId: data.sessionId }),
  });

  const parsed_response = await response.json();

  if (response.status !== 200) {
    console.error('[Metalitix]', parsed_response.message);
    return undefined;
  }

  return parsed_response;
}

export async function sendXRAnalyticsData(data: XRAnalytics.BatchRecord, clientIPAddress: string | undefined) {
  return new Promise(async resolve => {
    let headers: {
      [keys: string]: string;
    } = {};
    headers['Content-Type'] = 'application/json';
    if (clientIPAddress) {
      headers['X-Forwarded-For'] = clientIPAddress;
    }

    const response = await fetch(`${API_URL}/xr-analytics`, {
      method: 'POST',
      keepalive: true,
      headers,
      body: JSON.stringify(data),
    });

    const parsed_response = await response.json();

    if (response.status !== 200) {
      console.error('[Metalitix]', parsed_response.message);
      return undefined;
    }

    resolve(parsed_response);
  });
}

export async function sendMetricSurveysData(data: MetricSurveysData, showDebug: boolean) {
  showDebug && console.log('[Metalitix] survey data', data);

  const response = await fetch(`${API_ORIGIN}/api/v1/metric-surveys`, {
    method: 'POST',
    keepalive: true,
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify(data),
  });

  const parsed_response = await response.json();

  if (response.status !== 200) {
    console.error('[Metalitix]', parsed_response.message);
    return undefined;
  }

  return parsed_response;
}
