import platform from 'platform';
import MobileParser from 'device-detector-js/dist/parsers/device/mobiles';
import { ANode } from 'aframe';
import MetalitixLoggerAFrame8thWall from '../lib/mtx-8th-wall-a-frame';
import MetalitixLoggerAFrame from '../lib/mtx-poll-a-frame';
import MetalitixLoggerAFrameWithoutComponent from '../lib/mtx-poll-a-frame-without-component';
import { Object3D, PerspectiveCamera } from 'three';
import { MetalitixWarnings } from '../types';
let createDecipheriv: any;

if (process.env.TESTS) {
  createDecipheriv = require('browser-crypto').createDecipheriv;
} else {
  createDecipheriv = require('crypto').createDecipheriv;
}

export function deepEqual(x: any, y: any): boolean {
  const ok = Object.keys;
  const tx = typeof x;
  const ty = typeof y;

  return x && y && tx === 'object' && tx === ty
    ? ok(x).length === ok(y).length && ok(x).every(key => deepEqual(x[key], y[key]))
    : x === y;
}

export function isObject(item: any): item is Object {
  return item && typeof item === 'object' && !Array.isArray(item);
}

export function mergeDeep(target: any, source: any) {
  let output = Object.assign({}, target);
  if (isObject(target) && isObject(source)) {
    Object.keys(source).forEach(key => {
      if (isObject(source[key])) {
        if (!(key in target)) Object.assign(output, { [key]: source[key] });
        else output[key] = mergeDeep(target[key], source[key]);
      } else {
        Object.assign(output, { [key]: source[key] });
      }
    });
  }
  return output;
}

export const userAgent = window.navigator.userAgent;

export const isTablet =
  /(ipad|tablet|(android(?!.*mobile))|(windows(?!.*phone)(.*touch))|kindle|playbook|silk|(puffin(?!.*(IP|AP|WP))))/i.test(
    userAgent,
  );
export const isMobile =
  /Mobile|iP(hone|od|ad)|Android|BlackBerry|IEMobile|Kindle|NetFront|Silk-Accelerated|(hpw|web)OS|Fennec|Minimo|Opera M(obi|ini)|Blazer|Dolfin|Dolphin|Skyfire|Zune/.test(
    userAgent,
  );
const deviceType = isMobile ? 'mobile' : isTablet ? 'tablet' : 'desktop';
const mobileParser = new MobileParser();
const device = mobileParser.parse(window.navigator.userAgent);
export const systemInfo = {
  deviceModel: platform.product,
  deviceName: `${device.brand} ${device.model}`,
  deviceType,
  operatingSystemName: platform.os?.family,
  operatingSystemVersion: platform.os?.version,
};

export function decryptIamCredentials(text: string) {
  const [iv, encryptedData] = text.split(':');
  const encryptedText = Buffer.from(encryptedData, 'hex');

  const decipher = createDecipheriv(
    process.env.STREAM_ENCRYPTION_ALGORITHM ?? '',
    process.env.STREAM_SECRET_KEY ?? '',
    Buffer.from(iv, 'hex'),
  );

  let decrypted = decipher.update(encryptedText);
  decrypted = Buffer.concat([decrypted, decipher.final()]);
  return decrypted.toString();
}

export function delay(ms: number) {
  return new Promise(ok => setTimeout(ok, ms));
}

export function checkIsAFrameElementInside(parentElement: ANode, childElement: ANode): boolean {
  let stack: Array<ANode | Element> = [parentElement];

  while (stack.length > 0) {
    let currentElement = stack.pop();

    if (!currentElement) {
      continue;
    }

    if (currentElement === childElement) {
      return true;
    }

    const children = currentElement.children;
    for (let i = children.length - 1; i >= 0; i--) {
      stack.push(children[i]);
    }
  }

  return false;
}

export /**
 * Checks recursively if the camera is a descendant of the target or vice versa.
 * Uses uuid for comparison.
 * @param {ANode | null} scene - The camera node.
 * @param {Object3D | null} object3D - The target object.
 * @returns {string} - "Camera", "Scene", or "None" based on the relationship.
 */
async function checkForParentChildConflict(
  this: MetalitixLoggerAFrame8thWall | MetalitixLoggerAFrame | MetalitixLoggerAFrameWithoutComponent,
  camera: PerspectiveCamera | null,
  scene: ANode | null,
  object3D: Object3D | null,
): Promise<MetalitixWarnings> {
  if (!scene || !object3D) {
    return MetalitixWarnings.None;
  }

  if (camera == null) return MetalitixWarnings.None;

  let object3DEl = (object3D as any).el;
  let cameraEl = (camera as any).el;

  if (!object3DEl || !cameraEl) return MetalitixWarnings.None;

  if (checkIsAFrameElementInside(object3DEl, cameraEl)) {
    return MetalitixWarnings.SceneParented;
  }

  if (checkIsAFrameElementInside(cameraEl, object3DEl)) {
    return MetalitixWarnings.CameraParented;
  }

  return MetalitixWarnings.None;
}

export const getBrowser = () => {
  var ua = navigator.userAgent,
    tem,
    M = ua.match(/(opera|chrome|safari|firefox|msie|trident(?=\/))\/?\s*(\d+)/i) || [];
  if (/trident/i.test(M[1])) {
    tem = /\brv[ :]+(\d+)/g.exec(ua) || [];
    return { name: 'IE', version: tem[1] || '' };
  }
  if (M[1] === 'Chrome') {
    tem = ua.match(/\b(OPR|Edge)\/(\d+)/);
    if (tem != null) return { name: tem[1].replace('OPR', 'Opera'), version: tem[2] };
  }
  M = M[2] ? [M[1], M[2]] : [navigator.appName, navigator.appVersion, '-?'];
  if ((tem = ua.match(/version\/(\d+)/i)) != null) M.splice(1, 1, tem[1]);
  return { name: M[0], version: M[1] };
};
