import { getXRAnalyticsDataStream, sendXRAnalyticsData } from '../services';
import {
  DEFAULT_INTERVAL_VALUE,
  DEFAULT_INACTIVITY_INTERVAL,
  MAXIMUM_BATCH_RECORDS_LENGTH,
  MAXIMUM_BATCH_SENDING_INTERVAL,
  MAXIMUM_SESSION_KEEPALIVE_TIME,
  INACTIVITY_MAXIMUM_BATCH_RECORDS_LENGTH,
} from '../constants';
import { ELoggerPoolState, MetalitixLoggerOptions, MetalitixWarnings, SurveyTheme, XRAnalytics } from '../types';
import { deepEqual, delay, getBrowser, systemInfo } from '../utils';
import { addSurvey } from './mtx-engagement-survey';
import { DataStream } from './mtx-data-stream';

export default abstract class MetalitixLoggerBase<T, K> {
  private apiKey: string;
  private apiVersion: string = 'v2'; // TODO: update when server side will be updated
  private interval: number = DEFAULT_INTERVAL_VALUE;
  private inactivityInterval: number;
  protected sessionId: string | null;
  private dataStream: DataStream | null;
  private pollRecords: XRAnalytics.Record[];
  private lastPollTimestamp: number;
  private lastRecordTimeStampBeforeSend: number = Date.now();
  private nextPoll: number;
  private pollInProgress: boolean;
  private previousCameraData: XRAnalytics.UserCamera | null;
  private customData: { [key: string]: any } = {};
  private attributeChangeCounter: number = 0;
  private previousData: { [key: string]: any } = {};
  private previousDataChanged: number;
  private userMeta: Partial<XRAnalytics.UserMetadata>;
  private showSurveyAutomatically: boolean;
  private surveyTheme?: SurveyTheme;
  private surveyLogo?: string;
  private autoSurveyShowInMs: number;
  private surveyTimer: number;
  private prevFPSTime: number;
  private frames: number;
  private currentFPS: number;
  private loopFPSRequestId: number;
  private previousAnimations: { [key: string]: XRAnalytics.Animation } = {};
  private inactivityListeners: Array<() => void> = [];
  protected cameraOrScene: T | null = null;
  protected object3D: K | null = null;
  protected trackAnimations: boolean;
  private clientSessionId: number | null = null;
  private clientSessionStartTime: number | null = null;

  private surveyMinInterval: number;
  private surveyMaxInterval: number;
  public showDebug: boolean;

  private inactivityCounter: number = 0;

  private addSessionStartListener: (listener: () => void) => void = listener => {
    if (!this.clientSessionId) return;
    this.sessionStartListeners.push({
      listener,
      clientSessionId: this.clientSessionId,
      createdTime: Date.now(),
    });
  };

  private sessionStartListeners: Array<{
    clientSessionId?: number;
    listener: () => void;
    createdTime: number;
  }> = [];
  protected clientIPAddress?: string;

  constructor(
    apiKey: string,
    options: MetalitixLoggerOptions = {},
    replaceSendMethods?: {
      sendRecordsData?: (data: { apiver: string; items: XRAnalytics.Record[] }) => Promise<any>;
      sendMetricSurveysData?: (data: { sessionId: string; rating: number }) => Promise<any>;
    },
  ) {
    this.replaceSendMethods = replaceSendMethods;
    const {
      userMeta = {},
      showSurvey = true,
      clientIPAddress = undefined,
      surveyTheme,
      surveyLogo,
      inactivityInterval = DEFAULT_INACTIVITY_INTERVAL,
      trackAnimations = true,
      surveyMinInterval = 30,
      surveyMaxInterval = 150,
      showDebug = false,
    } = options;
    this.apiKey = apiKey;
    this.userMeta = userMeta;
    this.inactivityInterval = inactivityInterval <= 5000 ? inactivityInterval * 1000 : inactivityInterval;
    this.sessionId = null;
    this.dataStream = null;
    this.previousCameraData = null;
    this.pollRecords = [];
    this.lastPollTimestamp = Date.now() - MAXIMUM_BATCH_SENDING_INTERVAL;
    this.pollInProgress = false;
    this.nextPoll = -1;
    this.previousDataChanged = Date.now();
    this.pollInProgress = false;
    this.showSurveyAutomatically = showSurvey;
    this.surveyTheme = surveyTheme;
    this.surveyMinInterval = surveyMinInterval;
    this.surveyMaxInterval = surveyMaxInterval;
    this.surveyLogo = surveyLogo;
    /** Show survey automatically in range between 30 seconds and 3 minutes */
    this.autoSurveyShowInMs =
      (this.surveyMinInterval + (this.surveyMaxInterval - this.surveyMinInterval) * Math.random()) * 1000;
    this.surveyTimer = -1;
    this.prevFPSTime = Date.now();
    this.frames = 0;
    this.currentFPS = 0;
    this.loopFPSRequestId = -1;
    this.trackAnimations = trackAnimations;
    this.clientIPAddress = clientIPAddress;
    this.showDebug = showDebug;

    this.showDebug && console.log('[Metalitix] Metalitix version ' + process.env.VERSION);
    this.showDebug &&
      console.log('[Metalitix] The Metalitix Logger has been initialized with the following settings: ', {
        apiKey,
        showSurvey,
        clientIPAddress,
        surveyTheme,
        surveyLogo,
        inactivityInterval,
        trackAnimations,
        showDebug,
      });

    const sendPositionLoop = async () => {
      if (!this.cameraOrScene) {
        this.nextPoll = window.setTimeout(() => sendPositionLoop(), this.interval);
        return;
      }

      const camera = await this.getCameraData(this.cameraOrScene);
      const data = await this.getPositionData(this.cameraOrScene, this.object3D);

      if (
        deepEqual(data, this.previousData) &&
        deepEqual(camera, this.previousCameraData) &&
        this.loggerPoolState == ELoggerPoolState.Active
      ) {
        this.inactivityCounter++;
      } else if (!deepEqual(data, this.previousData) || !deepEqual(camera, this.previousCameraData)) {
        this.inactivityCounter = 0;
      }

      if (data && camera) {
        this.previousData = data;
        this.previousCameraData = camera;
      }

      if (
        this.inactivityCounter > this.inactivityInterval / this.interval &&
        this.loggerPoolState == ELoggerPoolState.Active
      ) {
        this.endSessionByInactivity();
      } else if (
        this.inactivityCounter == 0 &&
        this.loggerPoolState == ELoggerPoolState.EndedByInactivity &&
        this.cameraOrScene
      ) {
        this.startSession(this.cameraOrScene, this.object3D ?? null);
      }

      this.nextPoll = window.setTimeout(() => sendPositionLoop(), this.interval);
      
      if (this.loggerPoolState == ELoggerPoolState.Active) {
        await this.addUserPosition();
      }

      if (
        this.inactivityCounter != 0 &&
        this.loggerPoolState == ELoggerPoolState.Active &&
        this.pollRecords.length >= INACTIVITY_MAXIMUM_BATCH_RECORDS_LENGTH
      ) {
        this.sendPosition();
      }

      if (
        this.inactivityCounter == 0 &&
        this.loggerPoolState == ELoggerPoolState.Active &&
        this.pollRecords.length >= MAXIMUM_BATCH_RECORDS_LENGTH
      ) {
        this.sendPosition();
      }

    };

    sendPositionLoop();
  }

  private loggerPoolState: ELoggerPoolState = ELoggerPoolState.Created;

  private replaceSendMethods?: {
    sendRecordsData?: (data: { apiver: string; items: XRAnalytics.Record[] }) => Promise<any>;
    sendMetricSurveysData?: (data: { sessionId: string; rating: number }) => Promise<any>;
  };

  private updateFPS = () => {
    this.frames++;

    let time = Date.now();

    if (time >= this.prevFPSTime + 1000) {
      this.currentFPS = (this.frames * 1000) / (time - this.prevFPSTime);

      this.prevFPSTime = time;
      this.frames = 0;
    }
  };

  private loopFPS = () => {
    this.loopFPSRequestId = requestAnimationFrame(() => {
      this.updateFPS();
      this.loopFPS();
    });
  };

  private stopFPSLoop = () => {
    cancelAnimationFrame(this.loopFPSRequestId);
  };

  public forceSend = () => {
    if (this.loggerPoolState !== ELoggerPoolState.Active) {
      this.showDebug &&
        console.warn(
          '[Metalitix] Attempted to send data but a session is not currently active. Please start or resume a session!',
        );
      return;
    }

    this.showDebug && console.log('[Metalitix] Executed forceSend()');

    this.sendPosition(true);
  };

  public setCustomField = (key: string, value: any) => {
    if (this.loggerPoolState == ELoggerPoolState.Paused) return;
    console.warn(
      '[Metalitix] This method is deprecated and will be removed in future versions. Please consider using an alternative method setAttribute().',
    );
    this.showDebug && console.log('[Metalitix] Executed setCustomField() with parameters', { key, value });
    this.customData[key] = value;
  };

  public removeCustomField = (key: string) => {
    console.warn(
      '[Metalitix] This method is deprecated and will be removed in future versions. Please consider using an alternative method clearAttribute().',
    );
    this.showDebug && console.log('[Metalitix] Executed removeCustomField() with parameters', { key });
    delete this.customData[key];
  };

  public clearAllCustomFields = () => {
    console.warn(
      '[Metalitix] This method is deprecated and will be removed in future versions. Please consider using an alternative method clearAttributes().',
    );
    this.showDebug && console.log('[Metalitix] Executed clearAllCustomFields()');
    this.customData = {};
  };

  public setAttribute = (chart: string, value: string | number | boolean) => {
    if (this.loggerPoolState == ELoggerPoolState.Paused) return;

    if (chart === undefined || chart === null || chart === '') {
      console.error(
        `[Metalitix] The key from setAttribute() expects a non-null and non-empty value but received a null or empty value.`,
      );
      return;
    }

    if (value === undefined || value === null || value === '') {
      console.error(
        `[Metalitix] The value from setAttribute() expects a non-null and non-empty value but received a null or empty value.`,
      );
      return;
    }

    if (!(typeof chart === 'string')) {
      console.error(
        `[Metalitix] The chart from setAttribute() expects a value of type string but received ${chart} of type ${typeof chart}`,
      );
      return;
    }

    if (typeof value !== 'string' && typeof value !== 'boolean' && typeof value !== 'number') {
      console.error(
        `[Metalitix] The value from setAttribute() expects a value of type string, boolean or number but received ${value} of type ${typeof value}`,
      );
      return;
    }

    const logData = (sessionId: string | null) => [
      '[Metalitix] Custom metric recorded',
      {
        sessionId: sessionId,
        type: 'attribute',
        chart: chart,
        value,
      },
    ];

    if (!this.sessionId && this.clientSessionId !== null)
      this.addSessionStartListener(async () => {
        this.attributeChangeCounter++;
        this.showDebug && console.log(...logData(this.sessionId));
        this.customData[chart] = value;
      });
    else if (this.sessionId) {
      this.attributeChangeCounter++;
      this.showDebug && console.log(...logData(this.sessionId));
      this.customData[chart] = value;
    }
  };

  public clearAttribute = (chart: string) => {
    this.showDebug && console.log('[Metalitix] Executed clearAttribute() with parameters', { chart });
    this.attributeChangeCounter++;
    delete this.customData[chart];
  };

  public clearAttributes = () => {
    this.showDebug && console.log('[Metalitix] Executed clearAttributes()');
    this.attributeChangeCounter += Object.keys(this.customData).length;
    this.customData = {};
  };

  public addInactivityListener = (cb: () => void) => {
    this.showDebug && console.log('[Metalitix] Executed addInactivityListener()');
    this.inactivityListeners.push(cb);
  };

  public clearInactivityListeners = () => {
    this.showDebug && console.log('[Metalitix] Executed clearInactivityListeners()');
    this.inactivityListeners = [];
  };

  private fireAllInactivityListeners = () => {
    this.inactivityListeners.forEach(inactivityListener => inactivityListener());
  };

  private getRecord = async (
    eventType: XRAnalytics.EventTypes,
    sessionId: string,
    { userEvent, camera }: XRAnalytics.RecordDataFields,
  ) => {
    const data = await this.getPositionData(this.cameraOrScene, this.object3D);
    let userMeta: Partial<XRAnalytics.UserMetadata> = {};

    userMeta = this.getUserMeta();

    if (data === undefined) {
      throw new Error('Data is required field!');
    }

    if (!deepEqual(this.previousData, data)) {
      this.previousData = data;
      this.previousDataChanged = Date.now();
    }

    const resultData = Object.assign({}, this.customData, data);

    const currentAnimations = this.getAnimationsData();
    const animations: XRAnalytics.Animation[] = [];
    currentAnimations.forEach(animation => {
      const previousAnimation = this.previousAnimations[animation.name];

      if (previousAnimation === undefined || !deepEqual(previousAnimation, animation)) {
        animations.push(animation);
      }

      this.previousAnimations[animation.name] = animation;
    });

    const base: XRAnalytics.RecordBase = {
      apiver: this.apiVersion,
      sessionId,
      timestamp: Date.now(),
      animations,
      data: resultData,
      userMeta,
    };

    if (this.currentFPS > 0) {
      base.metrics = base.metrics ?? {};
      base.metrics.fps = Math.round(this.currentFPS);
    }

    if (eventType === XRAnalytics.EventTypes.SessionStart) {
      console.assert(camera !== undefined, '"camera" is required for session start!');
      return Object.assign({}, base, { eventType, camera }) as XRAnalytics.SessionStartRecord;
    }

    if (eventType === XRAnalytics.EventTypes.SessionUpdate) {
      return Object.assign({}, base, { eventType, camera }) as XRAnalytics.SessionUpdateRecord;
    }

    if (eventType === XRAnalytics.EventTypes.SessionEnd) {
      return Object.assign({}, base, { eventType, camera }) as XRAnalytics.SessionEndRecord;
    }

    if (eventType === XRAnalytics.EventTypes.SessionPause) {
      return Object.assign({}, base, { eventType }) as XRAnalytics.SessionPauseRecord;
    }

    if (eventType === XRAnalytics.EventTypes.SessionResume) {
      return Object.assign({}, base, { eventType }) as XRAnalytics.SessionResumeRecord;
    }

    if (eventType === XRAnalytics.EventTypes.UserPosition) {
      return Object.assign({}, base, { eventType }) as XRAnalytics.UserPositionRecord;
    }

    if (eventType === XRAnalytics.EventTypes.UserInteraction) {
      console.assert(userEvent !== undefined, '"userEvent" is required for user interaction!');
      return Object.assign({}, base, { eventType, userEvent }) as XRAnalytics.UserInteractionRecord;
    }

    throw new Error('Unknown eventType: ' + eventType);
  };

  private addRecord = async (
    eventType: XRAnalytics.EventTypes,
    { userEvent, camera }: XRAnalytics.RecordDataFields,
  ) => {
    if (this.sessionId === null) {
      return;
    }

    const record = await this.getRecord(eventType, this.sessionId, { userEvent, camera });

    this.pollRecords.push(record);

    return record;
  };

  private getStats = (records: XRAnalytics.Record[]) => {
    let sessionEvents = 0;
    let sessionStates = 0;
    let sessionAttributes = this.attributeChangeCounter;

    records.forEach(record => {
      if (record.userEvent?.eventType === XRAnalytics.UserInteractionTypes.Event) {
        sessionEvents++;
      }

      if (record.userEvent?.eventType === XRAnalytics.UserInteractionTypes.State) {
        sessionStates++;
      }
    });

    this.attributeChangeCounter = 0;

    return {
      sessionAttributes,
      sessionEvents,
      sessionStates,
    };
  };

  private redirectPositionsToAppropriateStream = async (records: XRAnalytics.Record[]) => {
    const stats = this.getStats(records);

    // Custom data redirection
    if (process.env.CUSTOM_BUILD === '8thwall' && this.replaceSendMethods?.sendRecordsData) {
      await this.replaceSendMethods.sendRecordsData({
        apiver: this.apiVersion,
        items: records,
      });
    }
    // Kinesis
    else if (process.env.KINESIS?.toLowerCase() === 'true') {
      await this.dataStream?.sendData(records);
    }
    // Gateway
    else {
      const batchRecordsData = {
        appkey: this.apiKey,
        apiver: this.apiVersion,
        items: records,
      };

      await sendXRAnalyticsData(batchRecordsData, this.clientIPAddress);
    }

    this.showDebug &&
      console.log(`The following has been transmitted to the dashboard:`, {
        dataPoints: records.length,
        sessionAttributes: stats.sessionAttributes,
        sessionEvents: stats.sessionEvents,
        sessionStates: stats.sessionStates,
      });
  };

  private sendPosition = async (sendAll = false): Promise<void> => {
    let records = [...this.pollRecords];

    this.clearSessionPollRecords();
    if (records.length === 0) {
      return;
    }

    this.pollInProgress = true;

    try {
      if (records.find(record => record.eventType === XRAnalytics.EventTypes.SessionEnd)) {
        let lastRecords: XRAnalytics.Record[] = [];

        for (let i in records) {
          if (records[i].timestamp > this.lastRecordTimeStampBeforeSend) lastRecords.push(records[i]);
        }

        records = lastRecords;
      }

      this.lastRecordTimeStampBeforeSend = records[records.length - 1].timestamp;

      await this.redirectPositionsToAppropriateStream(records);

      this.pollRecords = this.pollRecords.slice(MAXIMUM_BATCH_RECORDS_LENGTH);

      if (sendAll) {
        return this.sendPosition(true);
      }

      this.lastPollTimestamp = Date.now();
    } catch (error) {
      if (error instanceof Error && error.message === 'The security token included in the request is invalid.') {
        // Small delay to not spam the Kinesis server until session will be ready to start
        await delay(2000);
        return;
      } else {
        this.showDebug && console.error('[Metalitix]', error);
      }
    } finally {
      this.pollInProgress = false;
    }
  };

  protected abstract getPositionData: (
    cameraOrScene: T | null,
    object3D: K | null,
  ) => Promise<XRAnalytics.Data | undefined>;
  protected abstract getCameraData: (cameraOrScene: T | null) => Promise<XRAnalytics.UserCamera | undefined>;
  protected abstract getCameraId: (cameraOrScene: T | null) => Promise<string | undefined>;
  protected abstract getAnimationsData: () => XRAnalytics.Animation[];
  protected abstract instanceOfObject3D: (object3D: any) => object3D is K;
  protected abstract checkForParentChildConflict: (
    cameraOrScene: T | null,
    object3D: K | null,
  ) => Promise<MetalitixWarnings>;

  private getUserMeta = (): Partial<XRAnalytics.UserMetadata> => {
    return Object.assign({}, this.userMeta, {
      userAgent: window.navigator.userAgent,
      pagePath: location.pathname,
      pageQuery: location.search,
      systemInfo,
    });
  };

  private addSessionStart = async () => {
    const camera = await this.getCameraData(this.cameraOrScene);
    this.previousCameraData = camera || null;

    return await this.addRecord(XRAnalytics.EventTypes.SessionStart, { camera });
  };
  private addSessionEnd = async () => {
    let camera = await this.getCameraData(this.cameraOrScene);

    if (deepEqual(camera, this.previousCameraData)) {
      /** Don't send the camera object if it was not changed */
      camera = undefined;
    }

    await this.addRecord(XRAnalytics.EventTypes.SessionEnd, { camera });
  };
  private addUserPosition = async () => {
    await this.addRecord(XRAnalytics.EventTypes.UserPosition, {});
  };
  private addSessionPause = async () => {
    await this.addRecord(XRAnalytics.EventTypes.SessionPause, {});
  };
  private addSessionResume = async () => {
    await this.addRecord(XRAnalytics.EventTypes.SessionResume, {});
  };
  private addSessionUpdate = async (camera?: XRAnalytics.UserCamera) => {
    await this.addRecord(XRAnalytics.EventTypes.SessionUpdate, { camera });
  };

  private addNextUserPositionAndUpdateCameraIfNeeded = async () => {
    const camera = await this.getCameraData(this.cameraOrScene);

    if (camera === undefined || deepEqual(camera, this.previousCameraData)) {
      this.addUserPosition();
    } else {
      this.addSessionUpdate(camera);
      this.previousCameraData = camera;
      this.previousDataChanged = Date.now();
    }
  };

  private clearSessionPollRecords = () => {
    this.pollRecords = [];
  };

  /**
   * In case with platform where position and direction of the camera didn't change
   * user will need to provide 3D object from the scene to calculate position of camera relative to this object
   **/
  private setObject3D = (object3D: K | null) => {
    this.object3D = object3D;
  };

  public startSession = (
    cameraOrScene: T,
    object3D:
      | (K & { object3D?: K } & (
            | {
                uuid?: string;
              }
            | undefined
          ))
      | null = null,
  ) => {
    if (cameraOrScene === null || cameraOrScene === undefined) {
      console.error('[Metalitix] Parameter is undefined or null when calling startSession()');
      return;
    }

    if (this.loggerPoolState === ELoggerPoolState.Active || this.loggerPoolState === ELoggerPoolState.Paused) return;

    this.cameraOrScene = cameraOrScene;
    if (object3D?.object3D !== undefined) {
      object3D = object3D.object3D;
    }
    if (object3D !== null && !this.instanceOfObject3D(object3D)) {
      throw new Error(
        `MetalitixLogger.startSession() expects an a-entity or Object3D as the second parameter, but instead it received a ${object3D}!`,
      );
    }

    this.clientSessionId = Math.random() * Date.now();
    this.clientSessionStartTime = Date.now();

    this.setObject3D(object3D);

    this.clearSessionPollRecords();
    this.loopFPS();

    let timeout = async () => {
      return new Promise(resolve => {
        setTimeout(() => {
          resolve(undefined);
        }, 2000);
      });
    };

    const asyncPart = async () => {
      var conflicts = await this.checkForParentChildConflict(cameraOrScene, object3D);

      if (conflicts == MetalitixWarnings.CameraParented) {
        this.showDebug &&
          console.warn(
            '[Metalitix] The camera element is parented to your scene. This may produce unexpected results when recording the camera position to the Metalitix dashboard.',
          );
      } else if (conflicts == MetalitixWarnings.SceneParented) {
        this.showDebug &&
          console.warn(
            '[Metalitix] The scene element is parented to your camera. This may produce unexpected results when recording the camera position to the Metalitix dashboard.',
          );
      }

      let sessionId: string;

      if (process.env.KINESIS?.toLowerCase() === 'true') {
        const dataStreamCredentials = await getXRAnalyticsDataStream({
          appkey: this.apiKey,
          clientIPAddress: this.clientIPAddress,
        });

        if (dataStreamCredentials == undefined) {
          return;
        }

        sessionId = dataStreamCredentials.sessionId;
        this.dataStream = new DataStream(dataStreamCredentials);
      } else {
        const crypto = window.crypto;

        sessionId = crypto.randomUUID();
      }

      this.sessionId = sessionId;
      this.previousDataChanged = Date.now();
      if (this.loggerPoolState == ELoggerPoolState.Created) {
        this.setUpTabVisibilityListener();
        await timeout();
      }
      this.loggerPoolState = ELoggerPoolState.Active;
      let startSessionRecord: XRAnalytics.SessionStartRecord =
        (await this.addSessionStart()) as XRAnalytics.SessionStartRecord;
      await this.sendPosition(true);

      this.showDebug &&
        console.log('[Metalitix] Session started', {
          sessionId: sessionId,
          camera: await this.getCameraId(cameraOrScene),
          scene: object3D !== null ? object3D.uuid : null,
          agent: startSessionRecord.userMeta.userAgent,
          url: window.location.href,
          browser: getBrowser(),
        });

      // Sends data if logged in before receiving a session ID
      for (let i in this.sessionStartListeners) {
        let listener = this.sessionStartListeners[i];

        if (
          this.clientSessionId === listener.clientSessionId &&
          this.clientSessionStartTime !== null &&
          this.clientSessionStartTime <= listener.createdTime
        ) {
          await listener.listener();
        }
      }

      if (this.showSurveyAutomatically) {
        this.setDelayedSurvey(sessionId, this.autoSurveyShowInMs, false);
      }
    };

    asyncPart();
  };

  private setDelayedSurvey(sessionId: string, autoSurveyShowInMs: number, force: boolean) {
    this.surveyTimer = window.setTimeout(() => {
      if (process.env.CUSTOM_BUILD === '8thwall') {
        this.replaceSendMethods?.sendMetricSurveysData &&
          addSurvey({
            sessionId,
            theme: this.surveyTheme,
            force,
            customSendMetricSurveysData: this.replaceSendMethods.sendMetricSurveysData,
            surveyLogo: this.surveyLogo,
            showDebug: this.showDebug,
          });
      } else {
        addSurvey({
          apiKey: this.apiKey,
          sessionId,
          theme: this.surveyTheme,
          force,
          surveyLogo: this.surveyLogo,
          showDebug: this.showDebug,
        });
      }

      this.showDebug &&
        console.log('[Metalitix] Executed showSurvey() with parameters', { surveyTheme: this.surveyTheme });
    }, autoSurveyShowInMs);
  }

  public pauseSession = async () => {
    if (this.loggerPoolState !== ELoggerPoolState.Active) return;

    /** If the session was paused we need to send all our data and stop collecting new items */
    this.loggerPoolState = ELoggerPoolState.Paused;
    this.stopFPSLoop();
    await this.addSessionPause();
    const pauseSessionRecord = await this.sendPosition(true);
    this.showDebug && console.log('[Metalitix] Paused session with ID', this.sessionId);
    return pauseSessionRecord;
  };

  public resumeSession = async () => {
    if (this.loggerPoolState !== ELoggerPoolState.Paused) return;

    if (Date.now() - this.lastPollTimestamp <= MAXIMUM_SESSION_KEEPALIVE_TIME && this.sessionId !== null) {
      /** If the session was resumed on time - we need to continue collect current session data */
      this.clearSessionPollRecords();
      await this.addSessionResume();
      this.sendPosition(true);
      this.loggerPoolState = ELoggerPoolState.Active;
      this.stopFPSLoop();
      this.loopFPS();
    } else if (this.cameraOrScene !== null) {
      /** If the session was resumed when server already has closed the session - we need to start new session */
      this.startSession(this.cameraOrScene, this.object3D ?? null);
    }

    this.showDebug && console.log('[Metalitix] Resumed session with ID', this.sessionId);
  };

  private setUpTabVisibilityListener() {
    let beforeUnloadFired = false;
    let visibilityChangeFired = false;
    let beforeUnloadTime = 0;
    let visibilityChangeTime = 0;

    window.addEventListener('beforeunload', event => {
      beforeUnloadFired = true;
      beforeUnloadTime = new Date().getTime();

      if (visibilityChangeFired && Math.abs(beforeUnloadTime - visibilityChangeTime) <= 500) {
        this.endSession();
      }
    });

    document.addEventListener('visibilitychange', async () => {
      if (document.visibilityState === 'hidden') {
        visibilityChangeFired = true;
        visibilityChangeTime = new Date().getTime();

        if (beforeUnloadFired && Math.abs(beforeUnloadTime - visibilityChangeTime) <= 500) {
          this.endSession();
          return;
        }

        await this.pauseSession();
      } else {
        await this.resumeSession();
      }
    });
  }

  private endSessionBase = async () => {
    this.sessionId = null;
    this.clientSessionId = null;
    this.clientSessionStartTime = null;
    this.stopFPSLoop();
    await this.sendPosition(true);
    this.dataStream = null;
    this.clearSessionPollRecords();
  };

  private endSessionByInactivity = async () => {
    this.loggerPoolState = ELoggerPoolState.EndedByInactivity;

    if (Date.now() - this.lastPollTimestamp < MAXIMUM_SESSION_KEEPALIVE_TIME) {
      /** Don't send record with session end in case if server already close this session */
      await this.addSessionEnd();
    }

    this.endSessionBase();
  };

  public endSession = async (forceClearCustomData = false) => {
    this.loggerPoolState = ELoggerPoolState.Ended;

    if (this.sessionId === null) {
      /** The session was already ended */
      return;
    }

    this.showDebug && console.log('[Metalitix] Ended session with ID', this.sessionId);

    if (Date.now() - this.lastPollTimestamp < MAXIMUM_SESSION_KEEPALIVE_TIME) {
      /** Don't send record with session end in case if server already close this session */
      await this.addSessionEnd();
    }

    this.cameraOrScene = null;

    if (forceClearCustomData) {
      this.clearAllCustomFields();
    }

    this.endSessionBase();
  };

  public isRunning = () => {
    const isPolledRecently = Date.now() - this.lastPollTimestamp < this.interval * 2;
    return this.sessionId !== null && (this.pollInProgress || this.pollRecords.length > 0 || isPolledRecently);
  };

  private updateUserMeta = (userMeta: Partial<XRAnalytics.UserMetadata>) => {
    if (userMeta === undefined) {
      return;
    }

    this.userMeta = userMeta;

    return this.addSessionUpdate();
  };

  private sendUserEvent = async (
    eventName: XRAnalytics.UserInteractionNames | string,
    eventType: XRAnalytics.UserInteractionTypes | string,
    target?: string | XRAnalytics.EventPoint | any,
    points?: XRAnalytics.EventPoint[],
    eventGroupName?: string,
    value?: string | boolean | number,
    params?: any,
  ) => {
    return new Promise(async (resolve, reject) => {
      const userEvent: XRAnalytics.UserEvent = {
        eventName,
        eventType,
        target,
        points,
        eventGroupName,
        params,
      };

      if (value !== undefined) {
        userEvent.params = {
          [eventName]: value,
        };
      }

      if (this.sessionId === null && this.clientSessionId !== null) {
        // Sends data if logged in before receiving a session ID
        this.addSessionStartListener(async () => {
          await this.addRecord(XRAnalytics.EventTypes.UserInteraction, { userEvent });
          this.sendPosition(true);
          resolve(true);
        });

        return;
      }

      if (this.sessionId) {
        await this.addRecord(XRAnalytics.EventTypes.UserInteraction, { userEvent });
        this.sendPosition(true);
        resolve(true);
      }
    });
  };

  public logCustomEvent = async (eventName: string, params: object) => {
    console.warn(
      '[Metalitix] This method is deprecated and will be removed in future versions. Please consider using alternative methods logState or logEvent.',
    );

    await this.sendUserEvent(
      eventName,
      XRAnalytics.UserInteractionTypes.Custom,
      undefined,
      undefined,
      undefined,
      undefined,
      params,
    );

    this.showDebug && console.log('[Metalitix] Executed logCustomEvent() with parameters', { eventName, params });
  };

  public logState = async (chart: string, value: string | boolean | number) => {
    if (this.loggerPoolState == ELoggerPoolState.Paused) return;

    if (chart === undefined || chart === null || chart === '') {
      console.error(
        `[Metalitix] The chart from logState() expects a non-null and non-empty value but received a null or empty value.`,
      );
      return;
    }

    if (value === undefined || value === null || value === '') {
      console.error(
        `[Metalitix] The value from logState() expects a non-null and non-empty value but received a null or empty value.`,
      );
      return;
    }

    if (!(typeof chart === 'string')) {
      console.error(
        `[Metalitix] The chart from logState() expects a value of type string but received ${chart} of type ${typeof chart}`,
      );
      return;
    }

    if (typeof value !== 'string' && typeof value !== 'boolean' && typeof value !== 'number') {
      console.error(
        `[Metalitix] The value from logState() expects a value of type string, boolean or number but received ${value} of type ${typeof value}`,
      );
      return;
    }

    await this.sendUserEvent(chart, XRAnalytics.UserInteractionTypes.State, undefined, undefined, undefined, value);

    this.showDebug &&
      console.log('[Metalitix] Custom metric recorded', {
        sessionId: this.sessionId,
        type: 'state',
        chart: chart,
        value,
      });
  };

  public logEvent = async (chart: string, value: string) => {
    if (this.loggerPoolState == ELoggerPoolState.Paused) return;

    if (chart === undefined || chart === null || chart === '') {
      console.error(
        `[Metalitix] The chart from logEvent() expects a non-null and non-empty value but received a null or empty value.`,
      );
      return;
    }

    if (value === undefined || value === null || value === '') {
      console.error(
        `[Metalitix] The value from logEvent() expects a non-null and non-empty value but received a null or empty value.`,
      );
      return;
    }

    if (!(typeof chart === 'string')) {
      console.error(
        `[Metalitix] The chart from logEvent() expects a value of type string but received ${chart} of type ${typeof chart}`,
      );
      return;
    }

    if (!(typeof value === 'string')) {
      console.error(
        `[Metalitix] The value from logEvent() expects a value of type string but received ${value} of type ${typeof value}`,
      );
      return;
    }

    await this.sendUserEvent(value, XRAnalytics.UserInteractionTypes.Event, undefined, undefined, chart, undefined);

    this.showDebug &&
      console.log('[Metalitix] Custom metric recorded', {
        sessionId: this.sessionId,
        type: 'event',
        chart: chart,
        value: value,
      });
  };

  public showSurvey = (surveyTheme?: SurveyTheme) => {
    clearTimeout(this.surveyTimer);

    if (this.sessionId === null) {
      return;
    }

    this.surveyTheme = surveyTheme;

    this.setDelayedSurvey(this.sessionId, 0, true);
  };
}
